@import url(reset.css);
* {
  text-decoration: none;
}

strong {
  background-color: #41588c;
  color: white;
  padding: 2px 5px;
  box-sizing: border-box;
  margin: 0 2px;
}

.container {
  padding: 0 calc(50% - 570px);
  height: auto;
  margin: 0 auto;
  box-sizing: border-box;
}

@media (max-width: 991px) {
  .container {
    padding: 0 60px;
  }
}

@media (max-width: 576px) {
  .container {
    padding: 0 40px;
  }
}

@media (max-width: 320px) {
  .container {
    padding: 0;
  }
}

.icon {
  width: 28px;
  position: relative;
  top: 8px;
}

.header {
  height: 100vh;
  display: flex;
  flex-direction: row;
}

.header__wrapper {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  width: 50%;
  justify-content: center;
  align-items: center;
}

.header__info {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 40px;
}

.header__name {
  font-family: "IBM Plex Mono", monospace;
  font-size: 22px;
  color: white;
  background-color: #41588c;
  margin-top: 10px;
}

.header__contact {
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
  align-content: space-between;
}

.header__img {
  display: flex;
  box-sizing: border-box;
  max-width: 200px;
  height: 200px;
}

.header__img img {
  border-radius: 50%;
  width: 100%;
}

.header__description {
  width: 100%;
  line-height: 2;
  font-size: 16px;
  font-family: "IBM Plex Mono", monospace;
  color: black;
}

.main__wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

@media (max-width: 991px) {
  .main__wrapper {
    flex-direction: column;
  }
}

.about {
  width: 50%;
}

.about__title {
  font-size: 20px;
  font-family: "IBM Plex Mono", monospace;
  margin-bottom: 40px;
  margin-top: 100px;
}

.about__title strong {
  background: #41588c;
  border-radius: 0;
}

.about__desc {
  font-size: 16px;
  font-family: "IBM Plex Mono", monospace;
  color: black;
  margin-bottom: 20px;
  line-height: 2;
}

@media (max-width: 991px) {
  .about {
    width: 100%;
  }
}

.contact__item {
  font-size: 16px;
  font-family: "IBM Plex Mono", monospace;
  color: black;
  display: flex;
  margin-right: 10px;
  color: black;
  text-align: center;
  margin-top: 10px;
}

.contact__item:hover {
  cursor: pointer;
  opacity: .7;
}

.contact__item a {
  display: block;
}

.portfolio {
  width: 50%;
  display: flex;
  flex-direction: column;
  margin-bottom: 100px;
}

.portfolio__title {
  margin-top: 100px;
  font-size: 20px;
  font-family: "IBM Plex Mono", monospace;
  margin-bottom: 40px;
  width: 50%;
  margin-right: 0;
  margin-left: auto;
}

.portfolio__title strong {
  background: #41588c;
  border-radius: 0;
}

.portfolio__img {
  position: relative;
  width: 50%;
  margin: 25px 0;
  margin-right: 0;
  margin-left: auto;
}

.portfolio__img:hover div {
  opacity: 1;
  transition: opacity .4s;
}

.portfolio__img img {
  position: relative;
  width: 100%;
  height: 100%;
  border-radius: 50px;
}

.portfolio__circle {
  position: absolute;
  width: 50px;
  height: 50px;
  background-color: #41588c;
  top: -15px;
  z-index: -1;
  left: -15px;
  clip-path: circle(50% at 50% 50%);
}

.portfolio__group {
  position: absolute;
  left: 0;
  bottom: 0;
  top: 0;
  right: 0;
  background-color: white;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  opacity: 0;
  border-radius: 50px;
  transition: opacity .4s;
}

.portfolio__link {
  font-size: 20px;
  font-family: "IBM Plex Mono", monospace;
  color: black;
}

.portfolio__link strong {
  background: #41588c;
  border-radius: 0;
}

.portfolio__link:hover {
  cursor: pointer;
  background-color: #41588c;
  color: white;
}

@media (max-width: 991px) {
  .portfolio {
    width: 100%;
  }
  .portfolio__title {
    width: 100%;
  }
  .portfolio__img {
    margin: 25px 0;
    width: 100%;
  }
  .portfolio__group {
    opacity: .8;
    left: 50%;
    border-radius: 0;
  }
}

@media (max-width: 768px) {
  .header__wrapper {
    width: 100%;
  }
}
